let transformFormData = function (data) {
    // Do whatever you want to transform the data
    let ret = ''
    for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
    }
    if (ret.length > 0) {
        ret = ret.substring(0, ret.length - 1);
    }
    return ret;
}

let getClass = function (o) { //
    return Object.prototype.toString.call(o).slice(8, -1);
}

let deepCopy = function (obj) {
    let result, oClass = getClass(obj);

    if (oClass === "Object") result = {}; //
    else if (oClass === "Array") result = []; //
    else return obj; //

    for (let i in obj) {
        let copy = obj[i];

        if (getClass(copy) === "Object") result[i] = deepCopy(copy); // obj[i],obj[i][i]
        else if (getClass(copy) === "Array") result[i] = deepCopy(copy); // obj[i],obj[i][i]
        else result[i] = copy; //
    }
    return result;
}

let deepClone = function (data) {
    let type = getClass(data);
    let obj;
    if (type === 'array') {
        obj = [];
    } else if (type === 'object') {
        obj = {};
    } else {
        //
        return data;
    }
    if (type === 'array') {
        for (let i = 0, len = data.length; i < len; i++) {
            obj.push(deepClone(data[i]));
        }
    } else if (type === 'object') {
        for (let key in data) {
            obj[key] = deepClone(data[key]);
        }
    }
    return obj;
}

module.exports = {
    transformFormData, deepClone, deepCopy
}